import { FlippingCard,FlippingCardBack,FlippingCardFront } from "react-ui-cards"
import "./Book.css"
import ProgressBar from "@ramonak/react-progress-bar";


function Book(props){
    // console.log(props)
    
    return(
        <>
        <FlippingCard className="tune" >
    <FlippingCardBack>
    {/* <div className="Book-front" >

<div className="Book-cover-page-url" style={{backgroundImage:`url(${props.cover_page_url})`}} ></div>
<ProgressBar 
completed={75}
bgColor="#ee41d6"
height="15px"
labelColor="white"
baseBgColor="#31d8dc"
labelSize="10px"
animateOnRender
maxCompleted={100}/>
</div> */}
<div className="Book-back" >

<div className="Book-info-div" >
    <div>
        <strong>Name : </strong> {props.name} 
    </div>
    <div>
        <strong>Author : </strong> {props.author} 
    </div>
    <div>
        <strong>Total Pages : </strong> {props.total_pages} 
    </div>
    <div className="ss" >
        <span className="ss-minus">-</span><span className="ss-text" >Pages read : </span> <span className="ss-inp">{props.pages_read}</span> <span className="ss-add">+</span>
    </div>
    <div>
        <strong>About : </strong> {props.about} 
    </div>

</div>

    <button  className="edit-book" >Edit</button>
</div>
    </FlippingCardBack>
    <FlippingCardFront>
        <div className="Book-front" >

        <div className="Book-cover-page-url" style={{backgroundImage:`url(${props.cover_page_url})`}} ></div>
        <ProgressBar 
    completed={parseInt(props.pages_read/props.total_pages * 100)}
    bgColor="#ee41d6"
    height="15px"
    labelColor="white"
    baseBgColor="#31d8dc"
    labelSize="10px"
    animateOnRender
    maxCompleted={100}/>
        </div>
        
    </FlippingCardFront>
</FlippingCard></>
    )
} 

export default Book